*{
    font-family: 'Inter', sans-serif;
}
navbar{
    display: block; 
    font-weight: 600;
    background-color: #1c1e24;
    overflow: auto;
    margin-right: -8px;
    margin-left: -8px;
    margin-top: -10px;
    margin-bottom: -15px;
}
.navbarLeft{
    display: flex;
    margin-left: 14px;
}
.navbarLeft h2{
    padding-left: 20px;
    color: #6ecbe6
}
.navbarRight{
    position: absolute;
    top: 0px;
    right: 0px;
    padding-top: 11px;
    padding-right: 20px;
    font-size: 17px;
    color: #c8ced7;
}
footer{
    position: absolute;
    bottom:0;
    right:0;
    color:#fdffff;
}
body{
    background-color: #23272c;
}
body h1{
    font-size: 35px;
    font-weight: 700;
    color: #fdffff;
    margin-top: 60px;
}
body .reactImage{
    position: absolute;
    width: 600px;
    top: 50%;
    left: 50%;
    margin-top: -300px;
    margin-left: -200px;
}
li{
    padding-bottom: 30px;
    font-size: 17px;
    color: #9ea2a7;
}